.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;
  button {
    margin-left: auto;
    margin-top: 50px;
  }
}

.checkout-header {
  width: 100%;
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid darkgrey;

  &__block {
    text-transform: capitalize;
    width: 23%;

    &:last-child {
      width: 8%;
    }
  }
}

.checkout-total {
  margin-top: 30px;
  margin-left: auto;
  font-size: 36px;
}

.checkout-test-warning{
  text-align: center;
  margin-top: 40px;
  font-size: 24px;
  color: red;
}